import React from 'react';
import Works from './Works.js';
import Makes from './Makes.js';
import Skills from './Skills.js';
import Likes from './Likes.js';
import Links from './Links.js';
import Prof from './Prof.js';
import Hamburger from './Hamburger.js';
const ChangeDoms = (props) => {
  const userInput = props.userInput;
  const flag = props.flag;
  // if (flag == false) {
  //   return (
  //     <div>
  //       I believe that you will search for my <strong>prof</strong>...first and
  //       foremost...
  //     </div>
  //   );
  // }
  if (userInput.toLowerCase() == 'work') {
    return <Works />;
  } else if (userInput.toLowerCase() == 'make') {
    return <Makes />;
  } else if (userInput.toLowerCase() == 'skill') {
    return <Skills />;
  } else if (userInput.toLowerCase() == 'like') {
    return <Likes />;
  } else if (userInput.toLowerCase() == 'link') {
    return <Links />;
  } else if (userInput.toLowerCase() == 'hamburger') {
    return <Hamburger />;
  } else if (flag == true) {
    return <Prof />;
  } else {
    return (
      <div>
        I believe that you will search for my{' '}
        <span className="fontLarge">prof</span>...first and foremost...
      </div>
    );
  }
};

export default ChangeDoms;
