import Typography from '@material-ui/core/Typography';
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';
import Link from '@material-ui/core/Link';
import ILLFROGS from '../images/hamburger/ILL_FROGS.jpg';
import HUNGRYHEAVEN from '../images/hamburger/HUNGRY_HEAVEN.jpg';
import CHATTY_CHATTY from '../images/hamburger/CHATTY_CHATTY.jpg';
import Jack37Burger from '../images/hamburger/Jack37Burger.jpg';
import SHOGUN_BURGER from '../images/hamburger/SHOGUN_BURGER.jpg';
import The_Burger_Stand_Fellows from '../images/hamburger/The_Burger_Stand_Fellows.jpg';
